import React from "react";
import Helmet from "react-helmet";
import NavbarFixed from '../components/NavbarFixed';
import Footer from '../components/Footer';
import TagList from '../components/common/TagList';

const styles = {
  description: {
    color: '#576366',
  },
};

export default function Template({
  data
}) {
  const post = data.markdownRemark;
  return (
    <div className="blog-post-container content">
     <Helmet title={`${post.frontmatter.title}`} />
      <NavbarFixed
        title="BLOG"
        canGoBack={true}
        backToUrl='/blog'
      />

      <div className="blog-post col-md-8 offset-md-2">
        <h2>{post.frontmatter.title}</h2>
        <h6 style={styles.description}>{post.frontmatter.date}</h6>
        <h6 style={styles.description}>{post.frontmatter.excerpt}</h6>
        <TagList data={post.frontmatter.tags} />
        <hr />
        <div
          className="blog-post-content"
          dangerouslySetInnerHTML={{ __html: post.html }}
        />
      </div>
      <Footer />
    </div>
  );
}

export const pageQuery = graphql`
  query BlogPostByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        excerpt
        tags
      }
    }
  }
`
;



